
.indexMenu {
  border-right: none;
  text-align: center;

  li {
    margin: 0;
    border-right: 3px solid transparent;
  }

  li:hover,
  li.ant-menu-item-selected {
    border-right-color: #1890ff;
    background: #e6f7ff;
  }
}

.indexList {
  border-left: 1px solid #ccc;
  .ant-list-item-action > li:first-child{
    margin-left: 50px;
  }
  
  .ant-list-item-meta-content{
    min-width: 220px;
  }
}

@media all and (max-width: 767px) {
  .indexMenu {
    li {
      border-right: none;
    }

    li:hover,
    li.ant-menu-item-selected {
      background: none;
    }
  }

  .indexList {
    border-left: none;
  }
}

